import React from 'react';

import Page from '../../Components/Page/Page';
import TextOrRecordInput from '../../Components/TextOrRecordInput/TextOrRecordInput';

const API_TOKEN = '47ad9870695533cb638d9fc29c179a21';
const API_ENDPOINT = {
	text: 'https://api.audd.io/findLyrics/',
	audio: 'https://api.audd.io/recognizeWithOffset/'
}

class IndexPage extends React.Component {
	constructor(props) {
		super(props);

		this.preRef = React.createRef();
	}

	send(res) {
		let url = API_ENDPOINT[res.type] + '?api_token=' + API_TOKEN;
		let data;
		if (res.type === 'audio') {
			data = new FormData();
			data.append("file", res.value);
		} else if (res.type === 'text')
			url += '&q='+encodeURIComponent(res.value);


		let xhr = new XMLHttpRequest();
		xhr.open('POST', url, true);

		xhr.onreadystatechange = () => {
			if (xhr.readyState !== 4) return;

			this.preRef.current.innerHTML = xhr.responseText;
		}

		xhr.send(data);
	}

	render() {
		return (
			<Page id="index" route="/">
				<p>Akinator tries to recognize and guess what song you are trying to find. You are welcome to type lyrics or hum song's rythm in microphone.</p>
				<TextOrRecordInput onSelect={this.send.bind(this)} />
				<br />
				<pre ref={this.preRef} />
			</Page>
		);	
	}
}

export default IndexPage;